import { useState } from "react";
import axios from "axios";
import Spinner from "./Spinner";
import { useHistory, useLocation } from "react-router";
import { useEffect } from "react/cjs/react.development";

let useQuery = () => new URLSearchParams(useLocation().search);

let Register = ({ setTest, setUser, setHiringManagerEmail }) => {
  let [email, setEmail] = useState("");
  let [name, setName] = useState("");
  let [college, setCollege] = useState("");
  let [phone, setPhone] = useState("");
  let [loading, setLoading] = useState(false);
  let history = useHistory();
  let query = useQuery();

  useEffect(() => {
    if (query.get("email")) {
      setHiringManagerEmail(query.get("email"));
    }
  }, []);

  //make the fields required

  return (
    <>
      {!loading ? (
        <div className="container">
          <div className="row">
            <div className="col-md-4 offset-md-4 mt-4">
              <h1 style={{ textAlign: "center" }}>Register</h1>
              <form
                className="mt-4"
                onSubmit={(e) => {
                  e.preventDefault();
                  setLoading(true);
                  axios
                    .post(
                      "https://livioscreeningapi.herokuapp.com/screening/new",
                      {
                        email,
                        phone,
                        college,
                        name,
                      }
                    )
                    .then((response) => {
                      let { user, test } = response.data;
                      setUser(user);
                      setTest(test);
                      setLoading(false);
                      history.push("/test");
                    })
                    .catch((error) => {
                      console.log(error);
                    });
                }}
              >
                <div className="mb-3">
                  <input
                    type="text"
                    placeholder="Username"
                    className="form-control"
                    id="name"
                    value={name}
                    onChange={(e) => {
                      setName(e.currentTarget.value);
                    }}
                  />
                </div>

                <div className="mb-3">
                  <input
                    placeholder="Email Address"
                    type="email"
                    className="form-control"
                    id="email"
                    value={email}
                    onChange={(e) => {
                      setEmail(e.currentTarget.value);
                    }}
                  />
                </div>

                <div className="mb-3">
                  <input
                    placeholder="Phone Number"
                    type="number"
                    className="form-control"
                    id="phone"
                    value={phone}
                    onChange={(e) => {
                      setPhone(e.currentTarget.value);
                    }}
                  />
                </div>

                <div className="mb-3">
                  <input
                    type="text"
                    placeholder="College"
                    className="form-control"
                    id="college"
                    value={college}
                    onChange={(e) => {
                      setCollege(e.currentTarget.value);
                    }}
                  />
                </div>

                <button
                  type="submit"
                  className="btn btn-primary "
                  style={{ width: "100%" }}
                >
                  Start Test
                </button>
              </form>
            </div>
          </div>
        </div>
      ) : (
        <Spinner />
      )}
    </>
  );
};

export default Register;
